@import "reset";
@import "mixins";
@import "vars";
@import "generated/sprite-svg";
html,
body {
  width: 100%;
  height: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

:root {
  font-family: $font-primary;
  line-height: 1.6;
  * {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-overflow-scrolling: touch;
  }
}

a,
button {
  transition: all 0.2s ease;
}

.chat__hea:before {
  content: " ";
  position: absolute;
  z-index: 1;
  top: 1px;
  left: 1px;
  right: 1px;
  bottom: 1px;
  border: 1px solid #ffea00;
}

.feed__shade {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  display: block;
  height: 125px;
  width: 100%;
  background: -moz-linear-gradient(top, rgba(30, 37, 62, 0.75) 0%, rgba(125, 185, 232, 0) 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(top, rgba(30, 37, 62, 0.75) 0%, rgba(125, 185, 232, 0) 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, rgba(30, 37, 62, 0.75) 0%, rgba(125, 185, 232, 0) 100%);
}

.wrapper {
  width: 100%;
  min-height: 100%;
  color: #7781a0;
  font-family: $font-primary;
  background-color: #293252;
  box-sizing: border-box;
  background-image: url(../img/bg.jpg);
  background-size: cover;
  background-position-x: center;
  background-repeat: no-repeat;
}

.topline {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 35px;
  font-size: 11px;
  font-weight: 700;
  color: #7781a0;
  text-shadow: 0px 2px 4px rgba(116, 96, 238, 0.32);
  background-image: linear-gradient(to top, #181d35, #252d47);
}

.topline__beta {
  display: flex;
  justify-content: center;
  align-items: center;
}

.topline__icon {
  padding-top: 4px;
  margin-right: 12px;
  .icon-stars {
    font-size: 13px;
    fill: #7781a0;
  }
}

.header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 65px;
  padding-left: 10.9%;
  padding-right: 2.05%;
  background-image: linear-gradient(to top, darken(#252d49, 3%), lighten(#2f3755, 2%));
  box-shadow: 0px 4px 36px 0 rgba(20, 24, 47, 0.16);
  border-top: 1px solid #363e61;
  border-bottom: 1px solid #1f253e;
  @media only screen and (max-width: 1080px) {
    padding-left: 2%;
  }
}

.menu__link {
  margin-right: 3.8vw;
  font-family: inherit;
  font-size: 14px;
  font-weight: bold;
  color: #7781a0;
  text-shadow: 0px 2px 4px rgba(28, 34, 55, 0.32);
  &:hover {
    color: #dfdeee;
    text-shadow: 0px 2px 4px rgba(28, 34, 55, 0.32);
  }
}

.menu__link.is-active {
  color: #dfdeee;
  text-shadow: 0px 2px 4px rgba(28, 34, 55, 0.32);
}

.profile {
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile__money {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 12px;
  padding-right: 12px;
  height: 40px;
  border-radius: 40px;
  background-color: rgba(20, 24, 47, 0.28);
}

.coin {
  width: 23px;
  height: 23px;
}

.profile__money-sum {
  margin-left: 13px;
  font-size: 13px;
  font-weight: bold;
  letter-spacing: 1.5px;
  color: #dfdeee;
}

.profile__divider {
  width: 1.5px;
  height: 33px;
  margin-left: 50px;
  margin-right: 47px;
  opacity: 0.4;
  mix-blend-mode: screen;
  background-color: #252d47;
}

.profile__player {
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile__name {
  margin-right: 14px;
  font-size: 13px;
  font-weight: 500;
  color: #dfdeee;
}

.profile__photo {
  img {
    width: 42px;
    height: 42px;
    border-radius: 3px;
  }
}

.main {
  .main-container {
    display: flex;
    justify-content: space-between;
    padding: 18px 16px 24px 20px;
  }
  .chat {
    width: 300px;
    border-radius: 5px;
    background-color: #242c47;
    box-shadow: -12px 0px 128px 0 rgba(0, 12, 81, 0.12);
    border: solid 1px #2c3553;
    background-color: #242c47;
  }
  .chat__head {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 82px;
    padding: 20px 15px 20px 20px;
    background-color: #242c47;
    .icon-flash {
      margin-right: 20px;
      padding-top: 5px;
      font-size: 20px;
      box-sizing: content-box;
    }
  }
  .chat__online {
    display: flex;
    align-items: center;
    font-size: 13px;
    opacity: 0.72;
    font-weight: bold;
    color: rgba(149, 159, 187, 0.72);
    text-shadow: 0px 4px 8px rgba(28, 34, 55, 0.32);
  }
  .chat__online-quantity {
    margin-left: 4px;
    background-image: linear-gradient(30deg, #66cc99, #92d375), linear-gradient(#dfdeee, #dfdeee);
    font-family: "Poppins";
    font-weight: 600;
    letter-spacing: 2.6px;
    text-align: left;
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    text-shadow: 0px 4px 8px rgba(28, 34, 55, 0.32);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    background-image: linear-gradient(30deg, #66cc99, #92d375), linear-gradient(#dfdeee, #dfdeee);
  }
  .chat__close {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    background-color: rgba(20, 24, 47, 0.32);
    &:hover {
      cursor: pointer;
    }
    .icon-close {
      padding-top: 2px;
      width: 16px;
      height: 16px;
      box-sizing: content-box;
    }
  }
  .chat__feed {
    display: flex;
    flex-direction: column;
    position: relative;
    padding: 0px 10px 0px 10px;
    border-top: 2px solid #2e3755;
    border-bottom: 2px solid #1a2139;
  }
  .chat__player {
    display: flex;
    align-items: center;
    padding: 0 10px;
    margin-top: 15px;
    margin-bottom: 15px;
  }
  .chat__bot {
    padding: 12px 10px 11px 10px;
    border-radius: 10px;
    background-color: #2c3552;
    box-shadow: 0px 4px 8px 0 rgba(28, 34, 55, 0.24);
    .player__name {
      background-image: linear-gradient(to top, #66cc99, #92d375), linear-gradient(#dfdeee, #dfdeee);
      -webkit-background-clip: text;
      background-clip: text;
      -webkit-text-fill-color: transparent;
      -webkit-background-clip: text;
      background-clip: text;
      -webkit-text-fill-color: transparent;
      background-image: linear-gradient(to top, #66cc99, #92d375), linear-gradient(#dfdeee, #dfdeee);
    }
    .icon-flash {
      padding-top: 2px;
      font-size: 20px;
      box-sizing: content-box;
    }
  }
  .player__ava {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 42px;
    height: 42px;
    min-width: 42px;
    height: 42px;
    margin-right: 12px;
    background-color: #232a45;
    border-radius: 4px;
    img {
      width: 42px;
      height: 42px;
      border-radius: 4px;
    }
  }
  .player__message {
    display: flex;
    flex-direction: column;
    font-size: 12px;
    line-height: 1.1;
    color: #a2abc5;
  }
  .player__name {
    display: block;
    margin-bottom: 5px;
    color: #dfdeee;
  }
  .chat__form-wrap {
    padding: 20px 20px 20px 20px;
    border-top: 1px solid rgba(67, 79, 116, 0.4);
  }
  .chat__form {
    width: 100%;
  }
  .chat__form-inner {
    display: flex;
    align-items: center;
  }
  .chat__input {
    flex-grow: 1;
    height: 40px;
    padding-left: 5px;
    padding-right: 5px;
    border-radius: 10px;
    color: #dfdeee;
    background-color: rgba(20, 24, 47, 0.28);
    border: 1px solid transparent;
    &:focus {
      outline: none;
    }
  }
  .chat__submit {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 11px;
    width: 42px;
    height: 42px;
    // min-width: 42px;
    // min-height: 42px;
    border-radius: 50%;
    background-image: linear-gradient(to left, #7460ee, #8f71f2);
    &:hover {
      cursor: pointer;
      background-image: linear-gradient(to left, darken(#7460ee, 3%), darken(#8f71f2, 3%));
    }
    .icon-arrow-up {
      font-size: 20px;
    }
  }
}

// CONTENT
.content {
  flex-grow: 1;
  padding-top: 30px;
  max-width: 1180px;
  margin-left: 30px;
  margin-right: 30px;
  .heading {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 32px;
  }
  .heading__wager {
    margin-right: 11px;
    color: #dfdeee;
    text-shadow: 0px 6px 4px rgba(28, 34, 55, 0.1);
  }
  .heading__sum {
    background-image: linear-gradient(to left, #7460ee, #8f71f2), linear-gradient(#dfdeee, #dfdeee);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    text-shadow: 0px 6px 4px rgba(28, 34, 55, 0.1);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    background-image: linear-gradient(to left, #7460ee, #8f71f2), linear-gradient(#dfdeee, #dfdeee);
  }
  .range {
    margin: 0 auto;
    width: 517px;
    margin-top: 19px;
    padding: 18px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    @media only screen and (max-width: 1080px) {
      width: 100%;
    }
  }
  // custom input range
  .merged {
    width: 100%;
    /* color the track
   * firefox/webkit gradient done in JavaScript */
    input::-moz-range-track {
      border-radius: 8px;
      -webkit-appearance: none;
    }
    input::-ms-fill-upper {
      background-color: #262e4d;
    }
    input::-ms-fill-lower {
      background-color: #725fea;
    }
    input::-ms-fill-upper,
    input::-ms-fill-lower {
      border-radius: 8px;
      -webkit-appearance: none;
    }
    input::-ms-track {
      border: none;
      color: transparent;
    }
    input.webkit-track {
      border-radius: 8px;
      -webkit-appearance: none;
    }
    /* size the slider */
    input::-moz-range-track {
      height: 17px;
    }
    input::-ms-track {
      height: 17px;
    }
    input {
      height: 17px;
      width: 100%;
      &:focus {
        outline: none;
      }
    }
    /* style the thumb
   * can all be same but must be on seperate lines
   * or browsers won't accept them */
    input::-moz-range-thumb {
      width: 18px;
      height: 25px;
      border-radius: 8px;
      background-image: linear-gradient(to right, #7460ee, #8f71f2);
      border: solid 1px #8d6ff1;
      -webkit-appearance: none;
    }
    input::-ms-thumb,
    input::-webkit-slider-thumb,
    {
      width: 18px;
      height: 25px;
      border-radius: 8px;
      background-image: linear-gradient(to right, #7460ee, #8f71f2);
      border: solid 1px #8d6ff1;
      -webkit-appearance: none;
    }
    input::-webkit-slider-thumb {
      width: 18px;
      height: 25px;
      border-radius: 8px;
      background-image: linear-gradient(to right, #7460ee, #8f71f2);
      border: solid 1px #8d6ff1;
      -webkit-appearance: none;
    }
  }
  // end custom input range
  .range__create {
    padding: 0 35px;
    height: 42.5px;
    margin-top: 37px;
    border-radius: 42.5px;
    background-image: linear-gradient(to left, #7460ee, #8f71f2);
    border: none;
    font-size: 12px;
    font-weight: bold;
    color: #dfdeee;
    text-shadow: 0px 4px 8px rgba(28, 34, 55, 0.24);
    &:hover {
      color: #fff;
      cursor: pointer;
      background-image: linear-gradient(to left, darken(#7460ee, 3%), darken(#8f71f2, 3%));
    }
  }
  // battles
  .battles-wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-top: 20px;
    /* Medium Devices, Desktops */
    @media only screen and (max-width: 1366px) {
      justify-content: center;
    }
  }
  .battle {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 254px;
    margin: 0 10px 20px 10px;
    padding: 30px 34px;
    border-radius: 10px;
    background-color: #2c3552;
    box-shadow: 0px 5px 8px 0 rgba(28, 34, 55, 0.24);
    border: 1px solid #323b56;
    @media only screen and (max-width: 992px) {
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  .battle--join {
    .battle__btn {
      background-color: rgba(75, 104, 84, 0.2);
      border-color: #4b6854;
      &:hover {
        background-color: rgba(75, 104, 84, 0.4);
        border-color: #92d375;
      }
      span {
        background-image: linear-gradient(to left, #66cc99, #92d375), linear-gradient(#dfdeee, #dfdeee);
        -webkit-background-clip: text;
        background-clip: text;
        -webkit-text-fill-color: transparent;
        text-shadow: 0px 3px 2px rgba(28, 34, 55, 0.1);
        -webkit-background-clip: text;
        background-clip: text;
        -webkit-text-fill-color: transparent;
        background-image: linear-gradient(to left, #66cc99, #92d375), linear-gradient(#dfdeee, #dfdeee);
      }
    }
  }
  .battle__players {
    margin-bottom: 25px;
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
  .battle__player {
    flex-basis: 42%;
    display: flex;
    flex-direction: column;
    align-items: center;
    .player__photo img {
      width: 50px;
      height: 53px;
      border-radius: 4px;
    }
    .player__name {
      margin-top: 5px;
      max-width: 80px;
      font-size: 12px;
      font-weight: bold;
      color: #dfdeee;
      word-wrap: break-word;
      text-shadow: 0px 4px 8px rgba(28, 34, 55, 0.24);
    }
  }
  .battle__vs img {
    padding-top: 17px;
    max-width: 33px;
  }
  .battle__btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 156px;
    height: 38px;
    margin-top: 29px;
    border-radius: 2.5px;
    background-color: rgba(253, 108, 180, 0.1);
    box-shadow: 0px 32px 32px 0 rgba(28, 34, 55, 0.18);
    border: solid 2px #73365f;
    font-size: 13px;
    font-weight: 700;
    span {
      background-image: linear-gradient(to left, #ff3271, #fd6cb4), linear-gradient(#dfdeee, #dfdeee);
      -webkit-background-clip: text;
      background-clip: text;
      -webkit-text-fill-color: transparent;
      text-shadow: 0px 6px 4px rgba(28, 34, 55, 0.1);
      -webkit-background-clip: text;
      background-clip: text;
      -webkit-text-fill-color: transparent;
      background-image: linear-gradient(to left, #ff3271, #fd6cb4), linear-gradient(#dfdeee, #dfdeee);
    }
    &:hover {
      cursor: pointer;
      border-color: #ff3271;
      background-color: rgba(253, 108, 180, 0.2);
      color: #fff;
    }
  }
}

// WINNERS
.winners {
  min-width: 149px;
  width: 149px;
  border-radius: 2.5px;
  background-color: #242c47;
  box-shadow: -12px 0px 128px 0 rgba(0, 12, 81, 0.12);
  border: solid 1px #2c3553;
  .winners__head {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 85px;
    border-bottom: 2px solid #1e253e;
    font-size: 13px;
    font-weight: bold;
    color: rgba(149, 159, 187, 0.72);
    text-shadow: 0px 4px 8px rgba(28, 34, 55, 0.32);
  }
  .icon-live {
    margin-right: 10px;
    width: 19px;
  }
  .winners__feed {
    position: relative;
    padding: 0 10px 10px 9px;
    border-top: 1px solid #2f3957;
    border-bottom: 2px solid #1e253e;
  }
  .winners__player {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 10px;
    padding: 14px 5px 12px;
    border-radius: 10px;
    background-color: #2c3552;
    box-shadow: 0px 4px 8px 0 rgba(28, 34, 55, 0.24);
  }
  .player__photo img {
    width: 46px;
    border-radius: 4px;
  }
  .player__name {
    max-width: 100px;
    word-wrap: break-word;
    margin-top: 5px;
    font-size: 11px;
    font-weight: bold;
    color: #dfdeee;
    text-shadow: 0px 4px 8px rgba(28, 34, 55, 0.24);
  }
  .profile__money {
    margin-top: 10px;
    padding-left: 7px;
    padding-right: 7px;
    height: 30px;
    border-radius: 14px;
    img {
      width: 17px;
      height: 17px;
    }
  }
  .profile__money-sum {
    margin-left: 10px;
    font-size: 10px;
    letter-spacing: 1.3px;
  }
  .winners__total {
    padding: 17px 10px;
    font-size: 13px;
    font-weight: bold;
    line-height: 2.09;
    text-align: left;
    color: #dfdeee;
    text-shadow: 0px 4px 8px rgba(28, 34, 55, 0.32);
    text-align: center;
    border-top: 1px solid #2f3957;
  }
  .total__number {
    margin-top: 10px;
    background-image: linear-gradient(to left, #7460ee, #8f71f2), linear-gradient(#dfdeee, #dfdeee);
    font-size: 24px;
    line-height: 1.04;
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    text-shadow: 0px 6px 4px rgba(28, 34, 55, 0.1);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    background-image: linear-gradient(to left, #7460ee, #8f71f2), linear-gradient(#dfdeee, #dfdeee);
  }
}

// FOOTER
.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 15px;
  padding-right: 15px;
  min-height: 36px;
  font-size: 11px;
  font-weight: bold;
  color: #434f74;
  text-shadow: 0px 2px 4px rgba(28, 34, 55, 0.32);
  background-image: linear-gradient(to top, #14182f, #202640);
  border-top: 1px solid #242c48;
  border-bottom: 1px solid #181d34;
  .crypto {
    display: flex;
    align-items: center;
  }
  .crypto__item {
    margin-left: 2.3vw;
  }
  .crypto__bitcoin {
    width: 57.5px;
  }
  .crypto__litecoin {
    width: 57.5px;
  }
  .crypto__ethereum {
    width: 84px;
  }
}

@import "media"